import Link from 'next/link'

export default function LPHeader() {
  return (
    <header className="bg-white border-b">
      <div className="max-w-6xl mx-auto p-6 flex items-center justify-between">
        <div className="text-xl font-bold text-dribla-700">Dribla</div>
        <nav>
          <Link href="/login" className="text-sm text-gray-600 hover:text-dribla-600 mr-4">
            Login
          </Link>
          <Link href="/cadastro" className="text-sm bg-dribla-600 text-white px-4 py-2 rounded-lg hover:bg-dribla-700">
            Cadastrar
          </Link>
        </nav>
      </div>
    </header>
  )
}
